.ToggleContainer {
    width: 30%;
    height: 100%;
}
.ToggleDrawer {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.ToggleDrawer div {
    width: 90%;
    height: 3px;
    background-color: white;
    border-radius: 20%;
}